* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
    user-select: none;
}

header {
    width: 100%;
    height: 56px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.navbar {
    width: 1200px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.navbar__logo {
    width: 40px;
    height: 40px;
    background-color: blue;
}

.navbar__left, .navbar__center {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.navbar__center {
    margin-left: 48px;
    height: 40px;
}

.btnSearch {
    height: 40px;
    width: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 16px;
    cursor: pointer;
    font-size: 20px;
}

.btnCart {
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 16px;
    cursor: pointer;
    font-size: 18px;
}

.Menu {
    display: flex;
}

.Menu .Menu-item {
    margin-right: 16px;
    font-size: 18px;
    font-weight: 600;
}

.titleCart {
    font-size: 18px;
    font-weight: 600;
}

.CartIcon {
    width: 40px;
    height: 40px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.navbar__right {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.Account {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 18px;
}