.HeaderBestSeller {
    margin: 12px 0;
}
.BestSeller {
    width: 1200px;
    margin-top: 32px;
}

.BlockBestseller {
    position: relative;
    height: 400px;
}

.btnLeft {
    position: absolute;
    z-index: 1;
    left: 0;
    top: calc(50% - 20px);
    cursor: pointer;
}

.btnRight {
    position: absolute;
    z-index: 1;
    right: 0;
    top: calc(50% - 20px);
    cursor: pointer;
}

.btnLeft, .btnRight {
    width: 40px;
    height: 40px;
    padding: 6px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 40px;
}

.Container-BestSeller {
    display: flex;
    justify-content: space-between;
    min-width: 1200px;
    overflow-x: scroll;
    overflow-y: scroll;
    white-space: nowrap;
    scroll-behavior: smooth;
}

.Container-BestSeller::-webkit-scrollbar {
    display: none;
}