.Footer {
    height: 56px;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.Footer__Center {
    width: 100%;
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.Footer__Center span {
    padding: 6px;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 16px;
    color: #3b3b3b;
}